.main-area{
    display: flex;
    background: #2c2779;
    width: 100%;
    height: auto;
    flex-direction: column;
}
.main-area h1{
    color: #fff;
    margin: 40px auto 0px auto;
    font-size: 50px;
}
.btn-area {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 10px 20px 0 20px;
    margin: 50px auto 0 auto;
}
.btn-area button{
    width: 280px;
    height: 70px;
    font-size: 23px;
    font-weight: 600;
    border-radius: 2rem;
    border: none;
    margin: 0 10px 0 10px;
    transition: all 0.5s ease;
}.country-btn{
    background: #fff;
    color: #2c2779;
}.global-btn{
    border: 2px solid #ffffff !important;
    background: transparent;
    color: #fff;
}
.country-btn:hover{
    background: rgb(241, 238, 238);
    transform: scale(1.01);
    transition: all 0.2s ease;
}
.global-btn:hover{
    background: #fff;
    color: #2c2779;
    transform: scale(1.01);
    transition: all 0.2s ease;
}
/* card */
.cards-main{
    min-width: 180px !important;
    /* 
    width: auto;
    padding: 5px 20px 0 20px; */
    height: 150px;
    border-radius: 1rem !important;
    margin: 40px 10px 0 auto !important;
}.display-area{
    display: flex;
    flex-direction: row;
    margin: 20px 30px 0 30px;
    justify-content: center;
}.Affected{
    background: #ff973b !important;
    color: #fff;
}.Death{
    background: #ff3b3c !important;
    color: #fff;
}.Affected h5,.Death h5,.recover h5,.active h5{
    font-size: 20px;
    font-weight: 600;
}
.Affected h2,.Death h2 ,.recover h2, .active h2{
    font-size: 30px;
    font-weight: 600;
}.recover{
    background: #31c95c !important;
    color: #fff;
}.active{
    background: #309afe !important;
    color: #fff;
}.card-span{
    width: 100px !important;
}.m2{
    /* display: flex;
    flex-direction: row !important; */
}
@media only screen and (max-width:700px) {
    .main-area{
        display: flex;
        background: #2c2779;
        width: 100%;
        height: 490px;
        flex-direction: column;
    }
    .main-area h1{
        color: #fff;
        margin: 40px 0 0px 40px;
        font-size: 30px;
        font-weight: 600;
    }
    .btn-area {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding: 10px 20px 0 20px;
        margin: 30px auto 0 auto;
    }
    .btn-area button{
        width: 180px;
        height: 60px;
