        color: #fff;
    }
    /* cards */
    .cards-main{
        min-width: 160px !important;
        /* 
        width: 95%;
        padding: 0 !important; */
        height: 120px;
        border-radius: 1rem !important;
        margin: 0px auto 0 auto !important;
    }.display-area{
        display: flex;
        flex-direction: row;
        margin: 30px 20px 0 20px;
        justify-content: center;
    }.Affected{
        background: #ff973b !important;
        color: #fff;
    }.Death{
        background: #ff3b3c !important;
        color: #fff;
    }.Affected h5,.Death h5,.recover h5,.active h5{
        font-size: 20px;
        font-weight: 600;
    }
    .Affected h2,.Death h2,.recover h2, .active h2 {
        font-size: 24px;
        font-weight: 600;
    }.recover{
        background: #31c95c !important;
        color: #fff;
    }.active{
        background: #309afe !important;
        color: #fff;
    }
    /* .recover h2, .active h2{
        font-size: 23px;
    }.recover h5,.active h5{
